.caro{
  position: relative;
}

.navy{
  position: absolute;
  top: 0%;
}
.cards-size-about{
    width: 100%;
    border-radius: 5px;
    position: relative;
    overflow: hidden;
}

.title-about{
    position: absolute;
    top: 25%;
    left: 5%;
    transform: translateY(-50%);
    color: #fff;
}
.card-text-about {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    line-height: 20px;     /* fallback */
    max-height: 100px;      /* fallback */
    -webkit-line-clamp: 3; /* number of lines to show */
    -webkit-box-orient: vertical;
}

.overlay{
    position: absolute;
    width: 100%;
    bottom: -100px;
    left: 0;
    transition: .5s;
    padding: 5px;
    background-color: darkgoldenrod;
    opacity: 0.75;
}

.cards-size-about:hover .overlay{
    bottom: 0px;
}


.img-size-about{
     height: 300px;
     width: 100%;
}
.cards-size-audio{
    width: 100%;
    border-radius: 5px;
    position: relative;
    background-color: #F1F3F4;
    border-bottom: 3px solid goldenrod;
}

.title-audio{
    position: absolute;
    top: 25%;
    left: 5%;
    transform: translateY(-50%);
    color: #fff;
}

.img-size-audio{
     height: 300px;
     width: 100%;
}

.cards-size-audio audio{
    outline: none;
}

.audio-div{
    display: flex;
    justify-content: right;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #2d3428;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-size{
    width: 17.5rem;
    overflow: hidden;
}

.card-text {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    line-height: 20px;     /* fallback */
    max-height: 50px;      /* fallback */
    -webkit-line-clamp: 2; /* number of lines to show */
    -webkit-box-orient: vertical;
    
 }

.img-size{
    width: 100%;
    height: 180px;
    transition: .5s;
 }

 .img-size:hover{
     transform: scale(1.1);
 }
